export default {
  props: ["bookId"], //书籍标识
  data() {
    return {
      //   菜单数据
      menu_data: [
        {
          icon: "menu",
          text: "目录",
          id: "1"
        },
        {
          icon: "left",
          text: "上一话",
          id: "2"
        },
        {
          icon: "right",
          text: "下一话",
          id: "3"
        },
        {
          icon: "nolike",
          text: "点赞",
          id: "4",
          ready: true
        },
        {
          icon: "nofav",
          text: "收藏",
          id: "5",
          ready: false
        }
      ]
    };
  },
  methods: {
    //菜单点击 传递事件给父组件，参数为当前菜单点击对象
    menuClick(v) {
      switch (v["text"]) {
        case "目录":
          this.$emit("getMenuParme", v);
          return "";
        case "上一话":
          this.$emit("getMenuParme", v);
          return "";
        case "下一话":
          this.$emit("getMenuParme", v);
          return "";
        case "点赞":
          v["ready"] = !v["ready"];
          this.$emit("getMenuParme", v);

          return "";
        case "收藏":
          v["ready"] = !v["ready"];
          this.$emit("getMenuParme", v);
          return "";
      }
    }
  },
  created() {}
};
